import { Component, OnInit } from '@angular/core';
import { FormComponent } from '../../shared/form/form.component';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../shared/services";


@Component({
	selector: 'app-admin',
	templateUrl: './admin.component.html',
	styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {
	addFormSubmitted:boolean=false;
	editFormSubmitted:boolean=false;
	displayAddModel=false;
	displayEditModel=false;
	//configuration for feedback message
	addFormIndicationMessage:Array<string>=["Success","NewAdminFormsIsSubmitted"];
	editFormIndicationMessage:Array<string>=["Success","ProfileEditSuccessfully"];
	// configuratoin for data table
	rowsOnPage:number = 10;
	sortBy:string = "";
	sortOrder:string = "";
	adminEdit:any;
	admins:any[];
	filterQuery:string="";
	constructor(private callApi:CallApiService,
				private reloadService:ApiReloadService) {
					this.reloadService.reload$.subscribe(
						(reload)=>{
						  if(reload){
							  // console.log("reload employees");
							  this.admins=null;
							  //reload list when new employee is added
							  setTimeout(() => this.getAdminList(ApiCmd.superuserList), 2000);
						  }
						}
					  );
				 }

	ngOnInit() {
		this.getAdminList(ApiCmd.superuserList);
	}

	getAdminList(url){
		this.callApi.getData(url)
			.subscribe((data)=>{
				this.admins=data;
			});
	}

	//accept eventEmitter to let user know form submitted
	addFormSubmit(event){
		this.closeAddModalBox();
		this.addFormSubmitted=event;
	}

	editFormSubmit(event){
		this.closeEditModalBox();
		this.editFormSubmitted=event;
	}

	editAdmin(admin){
		this.adminEdit=admin;
		this.openEditModalBox();
		console.log("Edit "+admin.s_email+" is clicked");
	}

	deleteAdmin(adminID){
		console.log("Delete "+adminID+" is clicked");
		let removeData:object={s_id: adminID};		
		this.callApi.postData(ApiCmd.removeSuperuser,JSON.stringify(removeData))
			.subscribe((data)=>{
				console.log("deleted "+adminID)
				console.log(data);
				this.reloadService.setReload();
			});
	}

	openAddModalBox():void{
		this.displayAddModel=true;
	}

	closeAddModalBox():void{
		this.displayAddModel=false;
	}

	openEditModalBox():void{
		this.displayEditModel=true;
	}

	closeEditModalBox():void{
		this.displayEditModel=false;
	}
}



