import { Component, OnInit,ViewChild} from '@angular/core';
import { TabService } from "../../shared/services";
import { AuthService,Profile } from "../../shared";
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd } from "../../shared/services";
import { User } from "../login/user";
import { ImageCropperComponent } from 'ngx-image-cropper';

@Component({
	selector: 'app-settings',
	templateUrl: './settings.component.html',
	styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit{

	//get the user data from authService 
	profile:Profile;
	//edit button is clicked
	editDetail:Boolean=false;
	formSubmitted:boolean;//form submit status
	indicationMessage:Array<string>=["Success","ProfileEditSuccessfully"]
	profileForm:FormGroup;
	profileFormValue:any;
	//check password
	checkForm:FormGroup;
	displayCheckForm:boolean=false;
	editInvalid:boolean=false;
	//project this user currently working
	project:any;
	currentTab:string="Profile";
	//edit photo
	photo:any;
	file:any;
	@ViewChild('fileInput')
	fileInput: any;
	//initial settings for the table
	rowsOnPage:number = 5;
	sortBy:string = "w_id";
	sortOrder:string = "asc";

	// Access level
	al:number
	constructor(private tabService:TabService, 
				private authService:AuthService, 
				private fb:FormBuilder, 
				private callApi:CallApiService) 
				{
					this.al = this.authService.getUserAccessLevel();
					this.tabService.currentProjectTab$.subscribe(
						tab=>{
							//set currentTab to clicked tab from tab.services.
							this.currentTab=tab;
						});
	}

	ngOnInit() {
		if(this.al==3){
			this.initData();
			this.checkForm = this.fb.group({
				password:['',[Validators.required]]
			});

		}
	}

	initData(){
		this.profile=this.authService.getUserData();
		console.log(this.profile);
		this.getAttachedList(ApiCmd.projectListAttached);
		this.profileForm = this.fb.group({
			name:[this.profile.info.name],
			dob:[this.profile.info.dob],
			email:[this.profile.email],
			contact:[this.profile.info.contact],
			position:[this.profile.info.position],
		});
	}

	editDetails(){
		this.editDetail=true;
	}

	//submit the edited data
	onSubmit(value){
		value['id'] = this.profile.id;
		this.profileFormValue = value;
		console.log(this.profileFormValue);

		//do crosschecking for whether password input is valid
		//call a modal box to request for use input password
		this.displayCheckForm = true;
	}
	
	checkPassword(value){
		this.checkForm.reset();
		value['id'] = this.profile.id;
		this.callApi.getData(`${ApiCmd.authenticate}`+ value['id'] +`/`+ value['password'] )
		.subscribe((response)=>{
			if(response.length>0 && response[0].u_id == this.profile.id){
				this.callApi.postWithFile(`${ApiCmd.editProfile}`,this.profileFormValue,this.photo)
				.subscribe((response)=>{
					// console.log(response);
					this.editDetail=false;
					this.formSubmitted=true;
					this.displayCheckForm=false;
					this.editInvalid=false;
					this.file = null;

					//re-retrieve user data
					let user = new User();
					let cred = {
						id:this.profileFormValue.email,
						pw:value['password']
					}
					// .subscribe(data=>{});
					// this.callApi.getData(`${}${}/${}`)
					this.callApi.postLoginData(ApiCmd.loginCredential,JSON.stringify(cred))
					.subscribe((response)=>{
						response = response[0];
						console.log(response);
						// uncomment when jwt is ready
						if (response && response.jwt_token) {
							console.log("valid");
							localStorage.setItem('token', response.jwt_token);
							this.authService.refreshCurrentUser();
							this.initData();
						}
						
						// let userData=[];
						// userData=response;
						// if(userData[0] == null){
						// 	console.log("userData is empty"+userData);
						// }else{
						// 	// uncomment when jwt is ready
							

						// 	//save the login credentials to the sessionStorage
						// 	sessionStorage.setItem('currentUser', JSON.stringify(userData));
						// 	this.authService.setUserData(userData);
						// 	this.initData();
						// }
					});
				});
			}else{
				this.editInvalid = true;
			}
		});
	}

	closeModalBox(){
		this.displayCheckForm = false;
	}

	logout(){
		this.authService.logout();
	}

	getAttachedList(url:string){
		this.callApi.getData(url+"/"+this.profile.id)
		.subscribe(data=>{
			this.project=data;
			for(let i=0;i<this.project.length;i++){
				this.project[i]['p_admin'] = this.profile.u_name;
			}
		});
	}

	imageChangedEvent: any = '';
	croppedImage: any = '';

	fileUpload(event){
		console.log(event);
		//preview
		if (event.target.files && event.target.files[0]&&this.isImage(event)) {
			var reader = new FileReader()
			reader.readAsDataURL(event.target.files[0]);
			
			reader.onload = (event:any) => {
				this.file = event.target.result;
			}
			this.photo=event.srcElement.files;
			this.imageChangedEvent = event;
			console.log(this.photo);
		}else{
			alert("Please upload image only");
			this.fileInput.nativeElement.value = "";
			this.file=null;
			this.photo=null;
		}
	}

	isImage(event):boolean{
		let file =event.target.files[0];
		let fileType = file["type"];
		fileType=fileType.split("/");
		if(fileType[0]=="image"){
			return true;
		}

		return false;
	}

	imageCropped(image: string) {
		let img = new Image();
		img.src = image;
		// console.log(img);
	}

}



