import { Injectable } from '@angular/core';

@Injectable()
export class PagesArrayService {
  pages = ["dashboard","map","projects","employee","report","alert","settings","superuser","routerconfig","supervisor"];
  constructor() { }
  getPagesArray(){
    return this.pages;
  }
}



