import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { FormComponent } from '../../../shared/form/form.component';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../../shared/services";

@Component({
  selector: 'app-edit-site-supervisor',
  templateUrl: './edit-site-supervisor.component.html',
  styleUrls: ['./edit-site-supervisor.component.scss']
})
export class EditSiteSupervisorComponent implements OnInit {

  @Input() supervisorToBeEdit: any;
  editSupervisorForm:any[];
  group:FormGroup;
  @Output()
  formSubmitSuccess=new EventEmitter<boolean>();

  constructor(private fb:FormBuilder) { 
  }

  ngOnInit() {
    console.log(this.supervisorToBeEdit)
	this.group=this.fb.group({
		id: [this.supervisorToBeEdit.u_id],
		name: [this.supervisorToBeEdit.u_name,[Validators.required,Validators.maxLength(50)]],
		email: [this.supervisorToBeEdit.u_email,[Validators.required,Validators.email,Validators.maxLength(50)]],
		password:['',[Validators,Validators.minLength(8)]],
		phone: [this.supervisorToBeEdit.u_contact,[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],			
		position: ['Supervisor'],
		projectID: [''],
		dob: ['',[Validators.required,Validators.maxLength(50)]],			
		photo: [''],
		blood: [this.supervisorToBeEdit.u_blood,[Validators.required,CustomValidators.validateBloodType,Validators.maxLength(3)]],
		emergencyContactName:[this.supervisorToBeEdit.u_emName,[Validators.required,Validators.maxLength(50)]],
		emergencyContactPhone: [this.supervisorToBeEdit.u_emContact,[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],
		height: [this.supervisorToBeEdit.u_height,[Validators.required,Validators.min(0),Validators.max(280)]],
		weight: [this.supervisorToBeEdit.u_weight,[Validators.required,Validators.min(10),Validators.max(200)]]
		
	});
	this.constructSupervisorForm();
  }

  formSubmit(event){
    console.log("add-admin form submitted successfully? " + event);
    this.formSubmitSuccess.emit(event);
	}

  constructSupervisorForm():void{
		this.editSupervisorForm=[
		{
			form_group:this.group,
			submit_url:ApiCmd.updateSupervisor,
			contain_image:true,
			button_name:"Edit",
			form_control_names:[
				{
					"input_name":"Name",
					"form_control_name":"name",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExampleName",
					"error_message":"NameError"
				},
				{
					"input_name":"EmailAddress",
					"form_control_name":"email",
					"type":{type:"email",options:[],accept:""},
					"placeholder":"ExampleEmail",
					"error_message":"EmailError"
				},
				{
					"input_name":"Password",
					"form_control_name":"password",
					"type":{type:"password",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"PhoneNumber",
					"form_control_name":"phone",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExamplePhone",
					"error_message":"RequiredError"
				},
				{
					"input_name":"ProjectID",
					"form_control_name":"projectID",
					"special":{type:"select",url:ApiCmd.projectListOngoing,kind:"project",var_loc:1},
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"DOB",
					"form_control_name":"dob",
					"special":{type:"date"},					
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"Photo",
					"form_control_name":"photo",
					"type":{type:"file",options:[],accept:"image/*"},
					"placeholder":"",
					"error_message":""
				},
				{
					"input_name":"Blood",
					"form_control_name":"blood",
					"special":{type:"blood",var_loc:0},
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"emergencyContactName",
					"form_control_name":"emergencyContactName",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"emergencyContactPhone",
					"form_control_name":"emergencyContactPhone",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"Height",
					"form_control_name":"height",
					"type":{type:"number",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"Weight",
					"form_control_name":"weight",
					"type":{type:"number",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				}
			]
			// form_control_names:[
			// 	{
			// 		"input_name":"Name",
			// 		"form_control_name":"name",
			// 		"type":{type:"text",options:[],accept:""},
			// 		"placeholder":"ExampleName",
			// 		"error_message":"NameError"
			// 	},{
			// 		"input_name":"EmailAddress",
			// 		"form_control_name":"email",
			// 		"type":{type:"email",options:[],accept:""},
			// 		"placeholder":"ExampleEmail",
			// 		"error_message":"EmailError"
			// 	},{
			// 		"input_name":"Password",
			// 		"form_control_name":"password",
			// 		"type":{type:"password",options:[],accept:""},
			// 		"placeholder":"",
			// 		"error_message":"PasswordError"
			// 	},{
			// 		"input_name":"CompanyID",
			// 		"form_control_name":"companyID",
			// 		"type":{type:"text",options:[],accept:""},
			// 		"placeholder":"",
			// 		"error_message":"RequiredError"
			// 	},{
			// 		"input_name":"Contact",
			// 		"form_control_name":"phone",
			// 		"type":{type:"text",options:[],accept:""},
			// 		"placeholder":"ExamplePhone",
			// 		"error_message":"ContactError"
			// 	},{
			// 		"input_name":"Photo",
			// 		"form_control_name":"photo",
			// 		"type":{type:"file",options:[],accept:"image/*"},
			// 		"placeholder":"",
			// 		"errorMessage":""
			// 	}
			// ]

		}
	];
	}

}



