import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CallApiService, ApiCmd } from "../../../../shared/services";
import { TranslateService,LangChangeEvent } from 'ng2-translate';

@Component({
	selector: 'app-send-command',
	templateUrl: './send-command.component.html',
	styleUrls: ['./send-command.component.scss']
})
export class SendCommandComponent implements OnInit {

	@Input() workerID;
	@Input() number;

	//configuration for feedback message
	commandSend:boolean=false;
	indicationMessage:Array<string>=new Array<string>();
	constructor(private CallApi:CallApiService,
				private translate:TranslateService) { }

	ngOnInit() {
	}

	sendCommand(event){
		let cancel = false;
		console.log(event);
		let data = {
			name : event,
		};
		if(event=="heartrate"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="HeartRateSent";
		}else if(event=="shutdown"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="ShutdownSent";
		}else if(event=="restart"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="RestartSent";
		}else if(event=="message"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
			let message;
			this.translate.get("Message").subscribe((res: string) => {
				message = prompt(res);
			});

			if(message==null){
				cancel = true;
			}else{
				data["data1"] = this.number;
				data["data2"] = message;
				console.log(data);
			}
		}
		else if (event == "pedometer"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
			let pedo;
			this.translate.get("PedometerPrompt1").subscribe((res: string) => {
				pedo = prompt(res);
			});

			while(pedo!='0'&&pedo!='1'&&pedo!=null){
				this.translate.get("PedometerPrompt2").subscribe((res: string) => {
					pedo = prompt(res);
				});
			}
			if(pedo==null){
				cancel = true;
			}else{
				data["data1"] = parseInt(pedo);
				data["data2"] = "";
			}
			
		}
		else if (event == "datainterval"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
			let setDataInterval;
			this.translate.get("SetDataIntervalPrompt1").subscribe((res: string) => {
				setDataInterval = prompt(res);
			});

			while( isNaN(parseInt(setDataInterval))&&setDataInterval!=null){
				this.translate.get("SetDataIntervalPrompt2").subscribe((res: string) => {
					setDataInterval = prompt(res);
				});
			}
			
			if(setDataInterval==null){
				cancel = true;
			}else{
				data["data1"] = parseInt(setDataInterval);
				data["data2"] = "";
			}
		}
		else if (event == "positioning"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
		}
		else if (event == "phrase"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
			let phrase;
			this.translate.get("PhrasePrompt").subscribe((res: string) => {
				phrase = prompt(res);
			});

			if(phrase==null){
				cancel = true;
			}else{
				data["data1"] = phrase;
				data["data2"] = "";
			}
			console.log(data);
		}
		else if (event == "reconnect"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
			let domain,port;
			this.translate.get("DomainPrompt").subscribe((res: string) => {
				domain = prompt(res);
			});

			if(domain==null){
				cancel = true;
			}else{
				this.translate.get("PortPrompt1").subscribe((res: string) => {
					port = prompt(res);
				});

				while(isNaN(parseInt(port))&&port!=null){
					this.translate.get("PortPrompt2").subscribe((res: string) => {
						port = prompt(res);
					});
				}

				if(port==null){
					cancel = true;
				}else{
					data["data1"] = domain;
					data["data2"] = port;
				}
				
			}


		}
		else if (event == "lowbat"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
			let lowbat;
			this.translate.get("LowBatPrompt1").subscribe((res: string) => {
				lowbat = prompt(res);
			});

			while(lowbat!='0'&&lowbat!='1'&&lowbat!=null){
				this.translate.get("LowBatPrompt2").subscribe((res: string) => {
					lowbat = prompt(res);
				});
			}
			if(lowbat==null){
				cancel = true;
			}else{
				data["data1"] = parseInt(lowbat);
				data["data2"] = "";
			}
		}
		else if (event == "findWatch"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
		}else if (event =="hrInterval"){
			this.indicationMessage[0]="Success";
			this.indicationMessage[1]="MessageSent";
			let interval;
			this.translate.get("HrIntervalPrompt1").subscribe((res: string) => {
				interval = prompt(res);
			});

			while((isNaN(parseInt(interval))||parseInt(interval)<300)&&interval!=null){
				this.translate.get("HrIntervalPrompt2").subscribe((res: string) => {
					interval = prompt(res);
				});
			}

			if(interval==null){
				cancel = true;
			}else{
				data["data1"] = parseInt(interval);
				data["data2"] = "";
			}
		}
		if(!cancel){
			this.translate.get("CommandConfirmation").subscribe((res: string) => {
			  if(confirm(res)){
				  this.CallApi.sendCommand(data, this.workerID).subscribe((response)=>{
					  let responseBody = response.text();
					// if(responseBody==""){
						this.commandSend = true;
						// }
						console.log(response);
					});
				}
			});

		}
	}

}



