import { Component, OnInit, AfterViewInit } from '@angular/core';
import { AuthService } from '../../shared';
import { User } from './user';
import { MessagingService } from '../../shared/services/messaging/messaging.service';

@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.scss']
})
export class LoginComponent implements AfterViewInit {
	background_image = 'assets/images/login_background.jpg';
	returnUrl: string;

	user = new User("","");
	submitted:boolean = false;
	rememberMe:boolean = false;
	accepted:boolean=false;
	validating:boolean=true;
	message;

	onSubmit(){
		this.submitted = true;
		this.validating=true;
		this.authService.login(this.user);
		//check for validity~~~~
		//check using user.username and user.password
	}

	constructor(private authService: AuthService,
				private msgService: MessagingService) { 
  		this.authService.isLoggedIn.subscribe(
			login=>{
				//set currentTab to clicked tab from tab.services.
				if(this.submitted){
					this.accepted=login;
					this.validating=false;
				}
				console.log(login);
				if(login){

					console.log("do push notification lo")
					
				}
			});
	}

	ngAfterViewInit(){
		this.authService.checkStorage();		
	}

	reset():void{
		this.submitted=false;
		this.accepted=false;
		this.validating=false;
	}

}



