import { Pipe, PipeTransform } from '@angular/core';
import * as _ from "lodash";

@Pipe({
  name: 'dateFilter'
})
export class DateFilterPipe implements PipeTransform {

  transform(value: any, min: Date, max: Date): any {
    // console.log(typeof(min));
    // console.log(min.getTime());
    let date1 = min.getTime();
    let date2 = max.getTime();
    return _.filter(value, row=>{
              return (new Date(row.timestamp).getTime()>=date1&&new Date(row.timestamp).getTime()<=date2);
            });
  }

}



