export class Profile{
	_id: string;
    _rev: string;
    id: string;
    type: string;
    u_login: Login;
    pw: string;
    info:Info;
    u_name: string;
    details?:Details;
    project: Project;
    email:string;
    company_id:string;
}

export class Login{
    u_pw: string;
    u_email: string;
}

export class Info{
    name:string;
    photo: string;
    contact: string;
    dob: string;
    position: string;
}

export class Project{
	p_id: string;
    p_name: string;
    p_location: string;
}

export class Details{
	u_bloodtype: string;
	u_height: string;
	u_weight: string;
}


