import { Component, OnInit, ViewEncapsulation, NgZone  } from '@angular/core';
import { CallApiService, ApiCmd } from "../../shared/services";
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { FormBuilder,FormGroup } from '@angular/forms';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import { DatePipe } from '@angular/common';
import { TranslateService } from 'ng2-translate';


@Component({
	selector: 'app-report',
	templateUrl: './report.component.html',
	encapsulation:ViewEncapsulation.None,
	styleUrls: ['./report.component.scss']
})
export class ReportComponent implements OnInit {
	typeOption:any[] = ["hr", "steps", "location", "sos", "alert"];
	deviceOrProjectOption:any[] = ["project","device"];
	typeList: any[] = [];
	projectList:any[] = [];
	userList:any[] = [];
	deviceOrProjectList:any[] = [];
	type:string;
	projectOrDevice:string;
	id:string;
	startDate: Date;
	endDate: Date;
	bsConfig: Partial<BsDatepickerConfig> = { containerClass: "theme-default", /*locale: "en-gb",*/ showWeekNumbers: false, dateInputFormat: 'YYYY/MM/DD ' };	
	constructor(private callApi: CallApiService,
				private datePipe: DatePipe,
				private translate:TranslateService) {
					this.getProjectList(ApiCmd.projectList);
					this.getUserList(ApiCmd.workerList);
					this.generateDropdownList(this.typeOption,this.typeList);
					this.generateDropdownList(this.deviceOrProjectOption,this.deviceOrProjectList);
	}

	ngOnInit() {
	}

	// download(){
	// 	new Angular2Csv(mock.data, 'test');
	// }

	selectedID(item){
		this.id = item.id;
		console.log(this.id);
	}

	selectedType(item){
		this.type = item.id;
		console.log(this.type);
	}

	selectedDeviceOrProject(item){
		if(this.projectOrDevice != item){
			this.id = null;
		}
		this.projectOrDevice = item.id;
		console.log(this.projectOrDevice);
	}

	download(){
		let options = {
			fieldSeparator: ',',
			quoteStrings: '"',
			decimalseparator: '.',
			showLabels: false,
			headers: [],
			showTitle: false,
			title: 'Report',
			useBom: false,
			removeNewLines: true
		  };
		// http://mmdt.ddns.net:8081/wecare/api.php/mdt_sandbox/print/{type}/{project/device}/{projectid/deviceid}/{startdate}/{enddate}
		let url = `${ApiCmd.reportDownload}/${this.type}/${this.projectOrDevice}/${this.id}/${this.datePipe.transform(this.startDate, 'yyyyMMdd')}/${this.datePipe.transform(this.endDate, 'yyyyMMdd')}`;
		console.log(url);
		this.callApi.getData(url)
		.subscribe((data)=>{
			let dataNeeded = data//data[0].dev_common;
			// console.log(dataNeeded);
			for(var key in dataNeeded[0]){
				options.headers.push(key);
			}
			console.log(options.headers);
			if(dataNeeded.length == 0 || dataNeeded === undefined){
				this.translate.get("DownloadDataFail").subscribe((res: string) => {
					alert(res);
				});
	  
			}else{
				new Angular2Csv(dataNeeded, `Report-${this.id}`,options);
				console.log(data);
			}
		})
	}

	getProjectList(url){
		this.callApi.getData(url)
		.subscribe((data) => {
			console.log(data);
			data.forEach((item) => {
				this.projectList.push({
					id: item.p_id,
					text:item.p_data.p_name
				});
			});
			console.log(this.projectList);
		});
	}

	getUserList(url){
		this.callApi.getData(url)
		.subscribe((data) => {
			console.log(data);
			data.forEach((item) => {
				this.userList.push({
					id: item.dev_id,
					text:item.w_id
				});
			});
			console.log(this.userList);
		});
	}

	generateDropdownList(option, list){
		// this.typeOption.forEach((item) => {
		// 	this.typeList.push({
		// 		id: item,
		// 		text: item
		// 	})
		// });
		option.forEach((item) => {
			list.push({
				id: item,
				text: item
			})
		});
	}

	

}

