import { Component, OnInit } from '@angular/core';
import { CallApiService, ApiCmd, ApiReloadService } from "../../shared/services";
import { Http } from '@angular/http';
import * as L from 'leaflet';
import * as L_draw from 'leaflet-draw';
// routerList
@Component({
  selector: 'app-router',
  templateUrl: './router.component.html',
  styleUrls: ['./router.component.scss']
})
export class RouterComponent implements OnInit {
  map: L.map;
  layer: L.Layer;
  initZoom = 8;
  initLatlng = [2.893666551820528,102.77709960937501];
  clickedLatLng:L.latlng;
  
  // table
  rowsOnPage:number = 10;
	sortBy:string = "";
	sortOrder:string = "";
  routers:any[];

  //add router 
  deleteRouterIndicationMessage:Array<string>=["Deleted","DeleteRouterFormsIsSubmitted"];
  deleteRouterSubmitted:boolean=false;
  addFormIndicationMessage:Array<string>=["Success","NewRouterFormsIsSubmitted"];
  addFormSubmitted:boolean=false;
  displayAddModel=false;
  
  // router delete checkbox
  routerToBeDelete=[];
  
  constructor(private CallApi:CallApiService,
              private reloadService:ApiReloadService,
              private http:Http,
              ) { 
                this.reloadService.reload$.subscribe(
                  (reload)=>{
                    if(reload){
                      // console.log("reload employees");
                      this.routers=null;
                      //reload list when new employee is added
                      setTimeout(() => this.getRouterList(ApiCmd.routerList), 2000);
                    }
                  }
                  );
              }

  ngOnInit() {
    this.getRouterList(ApiCmd.routerList);
  }

  setSelectedLatLng(e:L.latlng){
    this.clickedLatLng = e ;
    console.log(this.clickedLatLng);
  }
  
  /* All map functions */
  onMapReady(map: L.map){
    
    let self = this;
    var clickMarker;
    var clickMarkerIcon = L.icon({
      iconUrl: '../assets/images/clickmarker.png',
      iconAnchor: [12.5, 41]
    });
    var tableMarkerIcon = L.icon({
      iconUrl: '../assets/images/tablemarker.png',
      iconAnchor: [12.5, 41]
    });

    map.addEventListener('click', function (e){
      self.setSelectedLatLng(e.latlng);
      if(map.hasLayer(clickMarker)) map.removeLayer(clickMarker);
      clickMarker = L.marker(e.latlng, {icon:clickMarkerIcon});
      clickMarker.addTo(map);
    });

    var tableListener = setInterval(function(){
      console.log(self.routers);
      if(self.routers !== null){
        for(let router in self.routers){
          var latlng = [/*lat: */self.routers[router].lat, /*lng: */self.routers[router].lng];
          L.marker(latlng, {icon:tableMarkerIcon}).addTo(map);
        }
        clearInterval(tableListener);
      }
    },200);
  }

  getRouterList(url: string){
    this.CallApi.getData(url)
    .subscribe((data)=>{
      this.routers=data;
      console.log(data);
    })
  }

  toggleRouterDeletion(mac){

    var idx = this.routerToBeDelete.indexOf(mac);

    // Is currently selected
    if (idx > -1) {
      this.routerToBeDelete.splice(idx, 1);
    }

    // Is newly selected
    else {
      this.routerToBeDelete.push(mac);
    }

    console.log(this.routerToBeDelete);
  }

  deleteRouters(){
    let url = ApiCmd.removeRouter;
    let data = JSON.stringify({"data": this.routerToBeDelete});
    if(confirm("Are you sure you want to remove the selected Access Point?")){
      this.CallApi.postData(url, data)
        .subscribe((response)=>{
          this.reloadService.setReload();	
          this.deleteRouterSubmitted=true;
          console.log(response);
        });
    }
  }

  addFormSubmit(event){
    this.closeAddModalBox();
    console.log("addFormSubmit function event: "+event);
		this.addFormSubmitted=event;
	}

  openAddModalBox():void{
		this.displayAddModel=true;
	}

	closeAddModalBox():void{
		this.displayAddModel=false;
  }
  
  map_OpenStreetMap = L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {maxZoom: 18} );
  map_TianDiTuNormal = L.layerGroup([
    L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=vec_w&X={x}&Y={y}&L={z}', {maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7']} ),
    L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=cva_w&X={x}&Y={y}&L={z}', {maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7']})
  ]);  
    /* Initial settings */
  options = {
      layers: [
        this.map_OpenStreetMap
      ],
      zoom: this.initZoom,
      center: this.initLatlng
  };

}



