import { Component, OnInit, Input } from '@angular/core';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';
import { ActivatedRoute } from '@angular/router';
import { CallApiService,ApiCmd} from '../../../../shared/services';
import { DatePipe } from '@angular/common';
import * as L from 'leaflet';
import * as L_draw from 'leaflet-draw';
import { BsDatepickerConfig  } from 'ngx-bootstrap/datepicker';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-employee-history-map',
  templateUrl: './employee-history-map.component.html',
  styleUrls: ['./employee-history-map.component.scss']
})
export class EmployeeHistoryMapComponent implements OnInit {
  map: L.map;
  historyLayers = new L.FeatureGroup;
  workerHistTrail:Array<any>=[];
  dataLoaded:boolean=false;
  initZoom = 8;
  showMap:boolean = true;

  @Input()
  devID;

  //datepicker config
	bsConfig: Partial<BsDatepickerConfig> = { containerClass: "theme-default", showWeekNumbers: false, dateInputFormat: 'YYYY/MM/DD' };
  minDate:Date = new Date();
  maxDate:Date = new Date();
  Form:FormGroup;

  // timepicker configuration
  mstep:Number = 15;
  showMeridian:boolean = false;
  minTime:Date = new Date();
  maxTime:Date = new Date();

  //initial Combined datetime 
  minDateTime:Date ;
  maxDateTime:Date ;

  date: Date = new Date();
  settings = {
      bigBanner: true,
      timePicker: true,
      format: 'yyyy/MM/dd HH:mm',
      defaultOpen: false
  }

  constructor(private _route: ActivatedRoute,
              private callApi: CallApiService,
              private datePipe: DatePipe,
              private _bsDatepickerConfig:BsDatepickerConfig) {
                this._bsDatepickerConfig.dateInputFormat = 'YYYY/MM/DD';
                this.minDate.setDate(this.minDate.getDate()-1);
                this.maxDate.setDate(this.maxDate.getDate()+1);

                this.minTime = new Date(this.minDate.getFullYear(), this.minDate.getMonth(), this.minDate.getDate(), 0, 0, 0);
                this.maxTime = new Date(this.maxDate.getFullYear(), this.maxDate.getMonth(), this.maxDate.getDate(), 0, 0, 0);

                this.minDateTime = new Date(this.minDate.getFullYear(), this.minDate.getMonth(), this.minDate.getDate(), 
                this.minTime.getHours(), this.minTime.getMinutes(), this.minTime.getSeconds());
                this.maxDateTime = new Date(this.maxDate.getFullYear(), this.maxDate.getMonth(), this.maxDate.getDate(), 
                this.maxTime.getHours(), this.maxTime.getMinutes(), this.maxTime.getSeconds());
              }

  ngOnInit() {
    let id=this._route.snapshot.paramMap.get('id');
    // this.getWorkerHistLoc(`${ApiCmd.workerHisLoc}/${id}`);
    this.getWorkerHistLoc(`${ApiCmd.deviceHisLoc}/${this.devID}/${this.datePipe.transform(this.minDate, 'yyyyMMdd')}/${this.datePipe.transform(this.maxDate, 'yyyyMMdd')}`);
    
  }

  filter(minDate:Date,minTime:Date,maxDate:Date,maxTime:Date){

    console.log(this.map);
    // let self = this;
    // this.map.eachLayer(function (layer) {
    //   self.map.removeLayer(layer);
    // });
    // this.map.removeLayer(this.historyLayers);
    this.clearMap();
    this.minDateTime = new Date(minDate.getFullYear(), minDate.getMonth(), minDate.getDate(), 
    minTime.getHours(), minTime.getMinutes(), minTime.getSeconds());
    this.maxDateTime = new Date(maxDate.getFullYear(), maxDate.getMonth(), maxDate.getDate(), 
    maxTime.getHours(), maxTime.getMinutes(), maxTime.getSeconds());
    console.log(this.minDateTime.toISOString());
    console.log(this.maxDateTime.toISOString());
    this.getWorkerHistLoc(`${ApiCmd.deviceHisLoc}/${this.devID}/${this.datePipe.transform(this.minDate, 'yyyyMMdd')}/${this.datePipe.transform(this.maxDate, 'yyyyMMdd')}`,this.updateLayer);    

  }

  clearMap(){
    // for (var i in this.map._layers) {
    //   if (this.map._layers[i].options.format == undefined) {
    //       try {
    //           this.map.removeLayer(this.map._layers[i]);
    //       } catch (e) {
    //           console.log("problem with " + e + this.map._layers[i]);
    //       }
    //   }
    // }
    for(var i in this.map._layers) {
        if(this.map._layers[i]._path != undefined) {
            try {
                this.map.removeLayer(this.map._layers[i]);
            }
            catch(e) {
                console.log("problem with " + e + this.map._layers[i]);
            }
        }
    }
    this.historyLayers.clearLayers();
    console.log(this.historyLayers);
  }

  // onValueChange(value: Date): void {
  //   console.log(value[0].getTime()+","+value[1].getTime());
  // }
  // onDate1ValueChange(value: Date): void {
  //   this.bsRangeValue[0] = value;
  //   console.log(value);
  // }

  // onDate2ValueChange(value: Date): void {
  //   this.bsRangeValue[1] = value;
  //   console.log(value);
  // }
  // onMinDateSelect($event){
  //   this.minDate = $event;
  // }

  // onMaxDateSelect($event){
  //   this.maxDate = $event;
  // }

  getWorkerHistLoc(url:string,callback?){
    this.callApi.getData(url)
    .subscribe((data)=>{
      console.log(data);

      if(data&&data.length!=0){
        // let hisLoc=data[0].dev_common;
        let hisLoc = data;
        this.classifyWorkerHistLoc(hisLoc);
        if(this.workerHistTrail&&this.workerHistTrail.length!=0){
          this.options.center=this.workerHistTrail[this.workerHistTrail.length-1].latLng;
        }

      }
      this.dataLoaded=true;
      if(callback)
        callback(this);
    });
  }

  classifyWorkerHistLoc(hist:Array<any>){
    console.log(hist);
    this.workerHistTrail = [];
    hist.forEach((loc)=>{
      let latLng=L.latLng(loc.latitude,loc.longitude);
      this.workerHistTrail.push({"timestamp":this.datePipe.transform(loc.timestamp, 'yyyy/MM/dd HH:mm:ss'),"latLng":latLng,"zone":loc.zone});
      // this.workerHistLatLng.push(latLng);
      // this.workerHistTimeStamp.push(this.datePipe.transform(loc.timestamp, 'yyyy/MM/dd HH:mm:ss'));
    });
  }

  clickLatLng(timestamp,latLng:L.latLng){
      let zoom=20;
      console.log(latLng);
      console.log(this.map);
      if(latLng){
          this.map.setView(latLng,zoom);
          this.showPopup(timestamp,this.historyLayers);
      }else{
          alert("User location not found");
      }
  }

  showPopup(timestamp,lyrs){
      let layers=lyrs.getLayers();
      for(let lyr of layers){
          console.log(lyr);
          let text = lyr.getPopup().getContent().replace(/(<\/?[^<]*>)/g,"").trim();
          // console.log(worker_id);
          // console.log(w_id);
          console.log(text.indexOf(timestamp));
          if(text.indexOf(timestamp)>=0){
              console.log("same");
              lyr.openPopup();
          }
          
      }
  }

  options={
    layers:[L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png')],
    zoom:8,
      center: L.latLng(46.879966, -121.726909)
  };

  markerOptions = {  //required for having icon
    icon: L.icon({
      iconSize: [ 25, 41 ],
      iconAnchor: [ 13, 41 ],
      iconUrl: '/assets/map/marker-icon.png'
    })
  };


  onMapReady(map: L.Map){
      // this.latlngs.forEach(place=>{
      //   this.newMarker(place,this.markerOptions,"hello").addTo(map);
      // })
      this.map = map;
      let workerHistLatLng = [];
      for (var i = 0; i < this.workerHistTrail.length; i++) {
        // console.log(this.workerHistTrail[i]);
        let pop = 
        `
        <div>Timestamp: ${this.workerHistTrail[i].timestamp}</div>
        <div>Zone: ${this.workerHistTrail[i].zone}</div>
        `;
        // this.workerHistTrail[i].timestamp+"testing"
        this.newMarker(this.workerHistTrail[i].latLng,this.markerOptions,pop).addTo(this.historyLayers);
        workerHistLatLng.push(this.workerHistTrail[i].latLng);
      }
      this.map.addLayer(this.historyLayers);
      let polyline = L.polyline(workerHistLatLng,{color:'blue'}).addTo(this.map);
      this.map.fitBounds(polyline.getBounds());

      console.log('map is ready');
  }

  updateLayer(self){
    console.log("update Layer\nworker hist Trail");

    console.log(self.workerHistTrail);
    let workerHistLatLng = [];
    for (var i = 0; i < self.workerHistTrail.length; i++) {
      // console.log(self.minDateTime);
      // console.log(self.workerHistTrail[i].timestamp);
      // console.log(self.minDateTime.getTime());
      // console.log(new Date(self.workerHistTrail[i].timestamp).getTime());
      let workerTime = new Date(self.workerHistTrail[i].timestamp).getTime();
      if(workerTime>self.minDateTime.getTime() && workerTime<self.maxDateTime.getTime()){
        let pop = 
        `
        <div>Timestamp: ${self.workerHistTrail[i].timestamp}</div>
        <div>Zone: ${self.workerHistTrail[i].zone}</div>
        `;
        // self.workerHistTrail[i].timestamp+"testing"
        self.newMarker(self.workerHistTrail[i].latLng,self.markerOptions,pop).addTo(self.historyLayers);
        workerHistLatLng.push(self.workerHistTrail[i].latLng);

      }
    }
    self.map.addLayer(self.historyLayers);
    let polyline = L.polyline(workerHistLatLng,{color:'blue'}).addTo(self.map);
    self.map.fitBounds(polyline.getBounds());
  }

  /* Add new stuff */
  newMarker(latlng, options, popup: string): L.Marker{
      let marker = L.marker(latlng, options);
      if(popup != null){
          marker.bindPopup(popup);
      }
      return marker;
  }

}



