import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { User } from '../../component/login/user';
import { tokenNotExpired, JwtHelper } from 'angular2-jwt'; 
import { CallApiService, ApiCmd } from "../../shared/services";
import { sha256, sha224 } from 'js-sha256';

@Injectable()
export class AuthService {
  // jwt(when change to jwt, userData = currentUser, session storage= local storage, currentUser(in session storage) = token)
  // fakejwt="eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiIxMjM0NTY3ODkwIiwibmFtZSI6IkpvaG4gRG9lIiwiaWF0IjoxNTE2MjM5MDIyfQ.XbPfbIHMI6arZ3Y922BhjWgQzWXcXNrz0ogtVhfEd2o"
  currentUser: any; 

  private loggedIn = new BehaviorSubject<boolean>(false);
  // private userData:any[];
  
  get isLoggedIn() {
    return this.loggedIn.asObservable(); 
  }
  
  constructor(
    private router: Router,
    private callApi:CallApiService 
  ) {
    this.logoutWhenTokenExpired();
    let token = localStorage.getItem('token');
    if(token!=null){
      let jwt = new JwtHelper();
      this.currentUser=(jwt.decodeToken(token));
      if(this.currentUser!=null){
        this.loggedIn.next(true);
      }

    }
  }

  logoutWhenTokenExpired(){
    let token = localStorage.getItem('token');
    if(token!=null){
      let jwt = new JwtHelper();
      if(jwt.isTokenExpired(token)){
        alert("Your session expired, please login again");
        this.logout();
      }
      
    }

  }

  login(user: User){
    if (user.username !== '' && user.password != '' ) {
      // this.getCredential(`${ApiCmd.loginCredential}u/${user.username}/p/${user.password}`);
      // console.log(sha256(user.password));

      // testing
      // let jwt = new JwtHelper();
      // console.log("fake jwt");
      // console.log(jwt.decodeToken(this.fakejwt));
      this.getCredential(ApiCmd.loginCredential,user); 
    }else{
      this.loggedIn.next(false);
    }
  }

  //login directly if cache have this infomation
  checkStorage(){
    // uncomment when jwt is ready
    this.logoutWhenTokenExpired();
    let token = localStorage.getItem('token');
    if(token!=null){
      let jwt = new JwtHelper();
      this.currentUser=(jwt.decodeToken(token));
      if(this.currentUser!=null){
          this.loggedIn.next(true);
          if(this.getUserAccessLevel()==1){
            this.router.navigate(['/admin']);
          }else{
            this.router.navigate(['/']);      
          }   
      }
    }


    // console.log(this.userData);
    // if(this.userData!=null){
    //     this.loggedIn.next(true);
    //     if(this.getUserAccessLevel()==1){
    //       this.router.navigate(['/admin']);
    //     }else{
    //       this.router.navigate(['/']);      
    //     }
    // }
  }

  setUserData(userData:any[]){
    // uncomment when jwt is ready
    this.currentUser = userData;

    // this.userData = userData;
  }

  getUserData(){
    // uncomment when jwt is ready    
    return this.currentUser;

    // return this.userData[0];
  }

  refreshCurrentUser(){
        let jwt = new JwtHelper();
        this.currentUser = jwt.decodeToken(localStorage.getItem('token'));
  }

  getUserAccessLevel(){
    // uncomment when jwt is ready    
    return this.currentUser.al;
    // return this.userData[0].al;
  }

  // Get the data based on the credential
  getCredential(url:string,user:User):void{
    // this.callApi.getData(url)
    let cred = {
      id:user.username,
      pw:user.password
    }
    this.callApi.postLoginData(url,JSON.stringify(cred))
      .subscribe(data=>{
        // let result = response.json();
        this.currentUser = [];
        data = data[0];
        console.log(data);
        // uncomment when jwt is ready
        if (data && data.jwt_token) {
            console.log(data.jwt_token);
            localStorage.setItem('token', data.jwt_token);
          
            let jwt = new JwtHelper();
            this.currentUser = jwt.decodeToken(localStorage.getItem('token'));
            
            // wrong password or username
            if(this.currentUser.id==null){
              this.loggedIn.next(false);
              return false;
            }

            this.loggedIn.next(true);
            if(this.getUserAccessLevel()==1){
              this.router.navigate(['/admin']);
            }else{
              this.router.navigate(['/']);
            }
            return true; 
          }
          else{
            console.log("currentUser is empty"+this.currentUser);
            this.loggedIn.next(false);
            
            return false; 
          } 

          // this.userData=[];
          // this.userData=data;
          // if(this.userData[0] == null){
          //       console.log("userData is empty"+this.userData);
          //       this.loggedIn.next(false);
          //   }else{
          //     //save the login credentials to the sessionStorage
          //     sessionStorage.setItem('currentUser', JSON.stringify(this.userData));
          //     this.loggedIn.next(true);
          //     console.log(this.getUserAccessLevel());
          //     if(this.getUserAccessLevel()==1){
          //       this.router.navigate(['/admin']);
          //     }else{
          //       this.router.navigate(['/']);
          //     }
          //   }
        })
  }

  logout() {  
    // uncomment when jwt is ready    
    localStorage.removeItem('token');
    this.currentUser = null;

    // this.userData=[];
    // sessionStorage.removeItem("currentUser");                          
    this.loggedIn.next(false);
    this.router.navigate(['/login']);
    this.callApi.logout();
  }

}



