import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './component/login/login.component';
import { DashboardComponent } from './component/dashboard/dashboard.component';
import { MapComponent } from './component/map/map.component';
import { ProjectsComponent,ViewProjectComponent } from './component/projects';
import { ViewEmployeeComponent, EmployeeComponent } from './component/employee';
import { ReportComponent } from './component/report/report.component';
import { AlertComponent } from './component/alert/alert.component';
import { SettingsComponent } from './component/settings/settings.component';
import { HomeLayoutComponent,LoginLayoutComponent} from './layouts';
import { AdminComponent } from './component/admin/admin.component';
import { RouterComponent } from './component/router/router.component';
import { NoAccessComponent } from './component/no-access/no-access.component';
import { AuthGuard, UserAccessAuthGuard } from './shared';
import { SiteSupervisorComponent } from './component/site-supervisor/site-supervisor.component';


const routes: Routes = [
  {
    path: '',
    component:HomeLayoutComponent,
    canActivate:[AuthGuard],
    children:[
      { path: '',redirectTo:"/dashboard",pathMatch:'full'},
      { path: 'dashboard',canActivate:[UserAccessAuthGuard],component:DashboardComponent,data:{page:"dashboard"}},
      { path: 'map',canActivate:[UserAccessAuthGuard],component:MapComponent,data:{page:"map"}},
      { path: 'projects',canActivate:[UserAccessAuthGuard],component:ProjectsComponent,data:{page:"projects"}},
      { path: 'projects/:id',canActivate:[UserAccessAuthGuard],component:ViewProjectComponent,data:{page:"projects"}},
      { path: 'employee',canActivate:[UserAccessAuthGuard],component:EmployeeComponent,data:{page:"employee"}},
      { path: 'employee/:id',canActivate:[UserAccessAuthGuard],component:ViewEmployeeComponent,data:{page:"employee"}},
      { path: 'report',canActivate:[UserAccessAuthGuard],component:ReportComponent,data:{page:"report"}},
      { path: 'alert',canActivate:[UserAccessAuthGuard],component:AlertComponent,data:{page:"alert"}},
      { path: 'site-supervisor',canActivate:[UserAccessAuthGuard],component:SiteSupervisorComponent,data:{page:"supervisor"}},
      { path: 'settings',canActivate:[UserAccessAuthGuard],component:SettingsComponent,data:{page:"settings"}},
      { path: 'admin',canActivate:[UserAccessAuthGuard],component:AdminComponent,data:{page:"superuser"}},
      { path: 'router',canActivate:[UserAccessAuthGuard],component:RouterComponent,data:{page:"routerconfig"}},
      { path: 'no-access',component:NoAccessComponent,data:{page:"noaccess"}},
    ]
  },
  {
    path: '',
    component:LoginLayoutComponent,
    children:[{
      path:'login',
      component:LoginComponent
    }]
  },
  {
    path: '**',redirectTo:"/login"
  }
  // { path: 'login',component: LoginComponent,data:{page:"login"}},
  // { path: 'dashboard',component:DashboardComponent,canActivate: [AuthGuard],data:{page:"dashboard"}},
  // { path: 'map',component:MapComponent,canActivate: [AuthGuard],data:{page:"map"}},
  // { path: 'projects',component:ProjectsComponent,canActivate: [AuthGuard],data:{page:"projects"}},
  // { path: 'employee',component:EmployeeComponent,canActivate: [AuthGuard],data:{page:"employee"}},
  // { path: 'report',component:ReportComponent,canActivate: [AuthGuard],data:{page:"report"}},
  // { path: 'alert',component:AlertComponent,canActivate: [AuthGuard],data:{page:"alert"}},
  // { path: 'settings',component:SettingsComponent,canActivate: [AuthGuard],data:{page:"settings"}},
  // { path: '**',redirectTo:"/dashboard"}
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{useHash:true})],
  exports: [RouterModule]
})

export class AppRoutingModule { }



