	getDutyChart(url:string):void{
		this.callApi.getData(url).subscribe(
			data=>{
				this.dutyChart=data;
			});
	}

	
	getProjectList(url){
		this.callApi.getData(url)
		.subscribe(data=>{
			this.projectList=data;
			console.log(this.projectList);
			this.assignProjectToDropdown();
		})
	}

	assignProjectToDropdown(): void {
		this.projects=[];
		this.projects.push(
			{
				id:"All",
				text: "All"
			}
		);
		this.projectList.forEach((project) => {
			this.projects.push({
				id: project.p_id,
				text:project.p_data.p_name
			});
		});
		this.init.push(this.projects[0]);
	}

