import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { AuthService } from './auth.service';
import { PagesArrayService } from '../services';
import 'rxjs/add/operator/take';
import 'rxjs/add/operator/map';

@Injectable()
export class UserAccessAuthGuard implements CanActivate {
  constructor(
    private pageArrayService: PagesArrayService,
    private authService: AuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    let userAccessLevel = this.authService.getUserAccessLevel();
    console.log(userAccessLevel);
    const page = route.data.page;
    const pages = this.pageArrayService.getPagesArray();
    // 1 = system admin (mdt)
    // 2 = superuser
    // 3 = project

    switch(userAccessLevel){
      case 1:
        if(page==pages[7]||page==pages[8]||page==pages[6])
          return true;
          break;
        case 2:
        if(page==pages[0]||page==pages[1]||page==pages[2]||page==pages[3]||page==pages[4]||page==pages[5]||page==pages[6]||page==pages[9])
          return true;
          break;
        case 3:
          if(page==pages[0]||page==pages[1]||page==pages[2]||page==pages[3]||page==pages[4]||page==pages[5]||page==pages[6])
            return true;
          break;
    }

    this.router.navigate(['/no-access']);
    return false;
  }
}



