import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS  } from '@angular/common/http';
import { HttpModule, Http } from '@angular/http';
import { DatePipe } from '@angular/common';


import { AppComponent } from './app.component';
import { JwtInterceptor } from './shared/helpers/jwt.interceptor';
import { HeaderComponent } from './shared/header/header.component';
import { FooterComponent } from './shared/footer/footer.component';
import { AppRoutingModule } from './app-routing.module';
import { LoginComponent } from './component/login/login.component';
import { DashboardComponent } from './component/dashboard/dashboard.component';
import { MapComponent,CollapsibleComponent } from './component/map';
import { ProjectsComponent,ViewProjectComponent } from './component/projects/';
import { EmployeeComponent } from './component/employee/employee.component';
import { ReportComponent } from './component/report/report.component';
import { AlertComponent } from './component/alert/alert.component';
import { SettingsComponent } from './component/settings/settings.component';
import { SidenavComponent } from './shared/sidenav/sidenav.component';
import { ReactiveFormsModule } from '@angular/forms';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

//external library
import { DataTableModule } from 'angular2-datatable';
import { Ng2GoogleChartsModule } from 'ng2-google-charts';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { BsDatepickerModule } from 'ngx-bootstrap';
import { defineLocale } from 'ngx-bootstrap/bs-moment';
import { enGb} from 'ngx-bootstrap/locale';
defineLocale('en-gb', enGb);
import { SelectModule } from 'ng2-select';
import { AngularFontAwesomeModule } from 'angular-font-awesome';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';
import { LeafletDrawModule } from '@asymmetrik/ngx-leaflet-draw';
import { Ng4GeoautocompleteModule } from 'ng4-geoautocomplete';
import { TranslateModule } from 'ng2-translate';
import { TranslateLoader, TranslateStaticLoader } from "ng2-translate/src/translate.service";
import { ImageCropperModule } from 'ngx-image-cropper';
import { ModalDialogModule } from 'ngx-modal-dialog';
import { AngularDateTimePickerModule } from 'angular2-datetimepicker';
import { TimepickerModule } from 'ngx-bootstrap/timepicker';
import { AngularFireModule } from 'angularfire2';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase';
import 'firebase/messaging';

//animations
import { BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { TabComponent } from './shared/tab/tab.component';
import { TabsComponent } from './shared/tabs/tabs.component';

//services
import { TabService, CallApiService, ApiReloadService, PagesArrayService } from "./shared/services";
import { AuthGuard, AuthService, UserAccessAuthGuard } from "./shared";


//pipes
import { DataFilterPipe, SelectOptionsTranslatePipe } from './shared/';
import { ProjectsTableComponent } from './component/projects/projects-table/projects-table.component';
import { AddProjectComponent } from './component/projects/add-project/add-project.component';
import { ProjectDetailComponent } from './component/projects/add-project/project-detail/project-detail.component';
import { ProjectLocationComponent } from './component/projects/add-project/project-location/project-location.component';
import { ProjectWorkersComponent } from './component/projects/add-project/project-workers/project-workers.component';
import { AddEmployeeComponent } from './component/employee/add-employee/add-employee.component';
import { HomeLayoutComponent } from './layouts/home-layout/home-layout.component';
import { LoginLayoutComponent } from './layouts/login-layout/login-layout.component';
import { ViewEmployeeComponent } from './component/employee/view-employee/view-employee.component';
import { ProjectInfoComponent } from './component/projects/view-project/project-info/project-info.component';
import { ProjectMapComponent } from './component/projects/view-project/project-map/project-map.component';
import { ProjectViewWorkersComponent } from './component/projects/view-project/project-view-workers/project-view-workers.component';
import { HealthVisualComponent } from './component/dashboard/health-visual/health-visual.component';
import { CardComponent } from './shared/card/card.component';
import { SensorDataComponent } from './component/employee/view-employee/sensor-data/sensor-data.component';
import { SendCommandComponent } from './component/employee/view-employee/send-command/send-command.component';
import { OnDutyChartComponent } from './component/dashboard/on-duty-chart/on-duty-chart.component';

//shared component
import { InfoRowComponent,FeedbackComponent,LoadingComponent } from './shared';
import { FormComponent } from './shared/form/form.component';
import { AdminComponent } from './component/admin/admin.component';
import { EmployeeHistoryMapComponent } from './component/employee/view-employee/employee-history-map/employee-history-map.component';
import { DateFilterPipe } from './shared/pipes/date-filter.pipe';
import { AddAdminComponent } from './component/admin/add-admin/add-admin.component';
import { EditAdminComponent } from './component/admin/edit-admin/edit-admin.component';
import { RouterComponent } from './component/router/router.component';
import { AddRouterComponent } from './component/router/add-router/add-router.component';
import { NoAccessComponent } from './component/no-access/no-access.component';
import { SiteSupervisorComponent } from './component/site-supervisor/site-supervisor.component';
import { AddSiteSupervisorComponent } from './component/site-supervisor/add-site-supervisor/add-site-supervisor.component';
import { EditSiteSupervisorComponent } from './component/site-supervisor/edit-site-supervisor/edit-site-supervisor.component';
import { environment } from '../environments/environment';
import { MessagingService } from './shared/services/messaging/messaging.service';
import { EditProjectComponent } from './component/projects/view-project/edit-project/edit-project.component';

firebase.initializeApp(environment.firebase);
firebase.messaging();

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    LoginComponent,
    DashboardComponent,
    MapComponent,
    ProjectsComponent,
    EmployeeComponent,
    ReportComponent,
    AlertComponent,
    SettingsComponent,
    SidenavComponent,
    TabComponent,
    TabsComponent,
    DataFilterPipe,
    ProjectsTableComponent,
    AddProjectComponent,
    ProjectDetailComponent,
    ProjectLocationComponent,
    ProjectWorkersComponent,
    ProjectViewWorkersComponent,
    AddEmployeeComponent,
    HomeLayoutComponent,
    LoginLayoutComponent,
    ViewEmployeeComponent,
    ViewProjectComponent,
    ProjectInfoComponent,
    ProjectMapComponent,
    HealthVisualComponent,
    CardComponent,
    SensorDataComponent,
    SendCommandComponent,
    InfoRowComponent,
    OnDutyChartComponent,
    FeedbackComponent,
    CollapsibleComponent,
    FormComponent,
    AdminComponent,
    LoadingComponent,
    EmployeeHistoryMapComponent,
    DateFilterPipe,
    AddAdminComponent,
    EditAdminComponent,
    RouterComponent,
    AddRouterComponent,
    NoAccessComponent,
    SiteSupervisorComponent,
    AddSiteSupervisorComponent,
    EditSiteSupervisorComponent,
    SelectOptionsTranslatePipe,
    EditProjectComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    DataTableModule,
    Ng2GoogleChartsModule,
    FormsModule,
    HttpClientModule,
    HttpModule,
    AngularFontAwesomeModule,
    SelectModule,
    BsDatepickerModule.forRoot(),
    ReactiveFormsModule,
    NgxChartsModule,
    LeafletModule.forRoot(),
    LeafletDrawModule.forRoot(),
    Ng4GeoautocompleteModule.forRoot(),
    TranslateModule.forRoot({
        provide: TranslateLoader,
        useFactory: (http: Http) => new TranslateStaticLoader(http, '/assets/i18n', '.json'),
        deps: [Http]
    }),
    ImageCropperModule,
    ModalDialogModule.forRoot(),
    AngularDateTimePickerModule,
    TimepickerModule.forRoot(),
    // AngularFireModule.initializeApp(environment.firebase)
    
  ],
  providers: [
    DatePipe,
    TabService,
    ApiReloadService,
    CallApiService,
    AuthGuard,
    PagesArrayService,
    UserAccessAuthGuard,
    MessagingService,
    AngularFireAuth,
    AuthService,
    {provide: LocationStrategy,useClass: HashLocationStrategy},
    {provide: HTTP_INTERCEPTORS,useClass: JwtInterceptor,multi: true},
    ],
    bootstrap: [AppComponent]
})

export class AppModule { }


