import { Component,OnInit} from '@angular/core';
import { TranslateService } from 'ng2-translate';
import { CallApiService, ApiCmd } from './shared/services';
import { MessagingService } from './shared/services/messaging/messaging.service';
import { AuthService } from './shared';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit{
  message;
  chCountry=["CN","HK","TW"];
  constructor(public translate:TranslateService,
              private callApi:CallApiService,
              private msgService: MessagingService,
              private authService: AuthService) {
		// let userLang=navigator.language.split('-')[0];
              //  notification.
              this.authService.isLoggedIn.subscribe( loggedIn =>{
                if(loggedIn){
                  this.requestPush();
                }
              });   
  }

  requestPush(){
		this.msgService.getPermission();
		this.msgService.receiveMessage();
		this.message = this.msgService.currentMessage;
	}

  ngOnInit(){
    this.callApi.getData(ApiCmd.getApiCallerInfo)
    .subscribe((data)=>{
      console.log("country: "+data.country);
      let lang=this.checkCountry(this.chCountry,data.country);
      if(lang==null)lang="ch";
      this.changeLanguage(lang);
      this.changeMapScript(lang);
    });
    
  }

  checkCountry(chCountry:Array<String>,country:string):String{
    if(chCountry.includes(country))
      return "ch";
    else
      return "en";
  }

  changeLanguage(lang){
    this.translate.use(lang);
  }

  changeMapScript(lang)
  {
  // console.log("change map script "+lang);
    var s = document.createElement("script");
    s.type = "text/javascript";
    if(lang=="en")
      s.src = "https://maps.googleapis.com/maps/api/js?key=AIzaSyDRQ1_DGJjK0OqnN_TNGUoQul6QJuhF2VI&libraries=drawing,places,geometry";
    else if(lang=="ch")
      s.src = "https://www.google.cn/maps/api/js?v=3&libraries=places&region=cn&language=zh-CN&key=AIzaSyAeg8uvtlLzc1zwyNL1z_UXZwZzjBw0B2g";
    else
      console.log("problem at changeMapScript")
      s.innerHTML = null;
      s.id = "map";
      document.getElementById("googleMap").innerHTML = "";
      document.getElementById("googleMap").appendChild(s);
  }

}



