import { Component, OnInit, Output, EventEmitter, Input, ViewEncapsulation } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiCmd, CallApiService, ApiReloadService } from '../../../../shared/services';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import * as L from 'leaflet';
import { Http } from '@angular/http';

@Component({
  selector: 'app-edit-project',
  templateUrl: './edit-project.component.html',
  encapsulation: ViewEncapsulation.None,
  styleUrls: ['./edit-project.component.scss']
})
export class EditProjectComponent implements OnInit {
  @Output()
  formSubmitSuccess = new EventEmitter<Boolean>();
  @Input()
  project: any;
  projectToBeEdit: FormGroup;
  photo: any;
  fileInput: any;
  preview;
  initialAdmin = [];
  manager: any[];
  items: Array<any> = [];
  previousGeoData;
  locationInitialVal;

  //datepicker config
  bsConfig: Partial<BsDatepickerConfig> = { containerClass: "theme-default", /*locale: "en-gb",*/ showWeekNumbers: false, dateInputFormat: 'YYYY/MM/DD ' };
  bsValue: Date;
  constructor(private fb: FormBuilder,
    private callApi: CallApiService,
    private reloadService: ApiReloadService,
    private http: Http) { }

  ngOnInit() {
    console.log(this.project);
    this.getManagerList(ApiCmd.managerList);
    this.projectToBeEdit = this.fb.group({
      projectId: [this.project.p_id, [Validators.required, Validators.maxLength(50)]],
      projectName: [this.project.p_data.p_name, [Validators.required, Validators.maxLength(50)]],
      projectAdmin: [this.project.p_admin, [Validators.required]],
      projectStartDate: [new Date(this.project.p_data.p_start_date), [Validators.required]],
      projectLocation: [this.project.p_data.p_location, [Validators.required, Validators.maxLength(80)]],
      projectLatLng: [''],
      projectSize: [String(this.project.p_data.p_floor)],
      projectStories: [String(this.project.p_data.p_floor)]
    });
    console.log(this.project);
    let lnglat = { lat: this.project.p_data.p_latitude, lng: this.project.p_data.p_longitude };
    this.locationInitialVal = this.projectToBeEdit.controls["projectLocation"].value;
    this.previousGeoData = {
      response: true,
      data: {
        formatted_address: this.projectToBeEdit.controls["projectLocation"].value,
        geometry: {
          location: lnglat
        }
      }
    }//initialize last data
  }

  fileUpload(event) {
    //preview
    if (event.target.files && event.target.files[0] && this.isImage(event)) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);

      reader.onload = (event: any) => {
        this.preview = event.target.result;
      }
      this.photo = event.srcElement.files;

    } else {
      alert("Please upload image only");
      this.fileInput.nativeElement.value = "";
      this.preview = null;
    }
  }

  isImage(event): boolean {
    let file = event.target.files[0];
    let fileType = file["type"];
    fileType = fileType.split("/");
    if (fileType[0] == "image") {
      return true;
    }

    return false;
  }

  getManagerList(url) {
    this.callApi.getData(url)
      .subscribe(data => {
        this.manager = data;
        // console.log(manager);
        this.assignToDropdown();
      })
  }

  assignToDropdown(): void {
    this.manager.forEach((admin) => {
      this.items.push({
        id: admin.u_id,
        text: admin.u_name
      });
    });
    let adminId = this.projectToBeEdit.controls["projectAdmin"].value;
    this.initialAdmin = this.items.filter((obj) => {
      return obj.id == adminId;
    });
    console.log(adminId);
    console.log(this.initialAdmin)
  }

  public selected(value: any): void {
    this.projectToBeEdit.controls["projectAdmin"].patchValue(value.id);
    console.log('Selected value is: ', value);
  }

  onSubmit(value) {
    console.log(value);
    value['projectStartDate'] = (new Date(value['projectStartDate'])).toISOString();
    if (value['projectSize'] === '') value['projectSize'] = '0';
    if (value['projectStories'] === '') value['projectStories'] = '0';
    this.callApi.postWithFile(ApiCmd.editProject, value, this.photo)
      .subscribe((response) => {
        this.formSubmitSuccess.emit(true);
        console.log(response);
        this.reloadService.setReload();
      },
        (error) => {
          alert("There's something wrong");
        }
      );
  }

  // map related api
  editableLayers = new L.FeatureGroup();
  map: L.map;
  layer: L.Layer;
  latlng;

  map_OpenStreetMap = L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', { maxZoom: 18 });
  map_GoogleCNNormalMap = L.tileLayer('http://www.google.cn/maps/vt?lyrs=m@189&gl=cn&x={x}&y={y}&z={z}', { maxZoom: 18 });
  map_GoogleCNSatelliteMap = L.tileLayer('http://www.google.cn/maps/vt?lyrs=s,h@189&gl=cn&x={x}&y={y}&z={z}', { maxZoom: 18 });
  map_TianDiTuNormal = L.layerGroup([
    L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=vec_w&X={x}&Y={y}&L={z}', { maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7'] }),
    L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=cva_w&X={x}&Y={y}&L={z}', { maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7'] })
  ]);
  map_TianDiTuSatellite = L.layerGroup([
    L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=img_w&X={x}&Y={y}&L={z}', { maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7'] }),
    L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=cia_w&X={x}&Y={y}&L={z}', { maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7'] })
  ]);

  options = {
    layers: [
      this.map_OpenStreetMap
    ],
    zoom: 13,
    center: L.latLng(3.05, 101.62)
  };

  layersControl = {
    baseLayers: {
      'Open Street Map': this.map_OpenStreetMap,
      'Google Normal': this.map_GoogleCNNormalMap,
      'Google Satelite': this.map_GoogleCNSatelliteMap,
      'TianDiTu Normal': this.map_TianDiTuNormal,
      'TianDiTu Satellite': this.map_TianDiTuSatellite
    }
  };

  markerOptions = {  //required for having icon
    icon: L.icon({
      iconSize: [25, 41],
      iconAnchor: [13, 41],
      iconUrl: 'assets/marker-icon.png',
      shadowUrl: 'assets/marker-shadow.png'
    })
  };

  searchbox_Settings = {
    //refer to https://github.com/tanoy009/ng4-geoautocomplete for options
  };

  searchbox_AutoCompleteCallback(selectedData: any) {
    if (selectedData.response) {
      this.projectToBeEdit.controls["projectLocation"].patchValue(selectedData.data.formatted_address);
      var latlng = selectedData.data.geometry.location;    //the latlng <- but idk where u wanna get it larr
      this.projectToBeEdit.controls["projectLatLng"].patchValue(JSON.stringify(latlng));
      var map = this.map;
      map.setView(latlng);
      map.eachLayer(function (layer) {
        if ('_level' in layer) return;
        map.removeLayer(layer);
      });
      this.layer = L.marker(latlng, this.markerOptions);
      map.addLayer(this.layer);
      this.map = map;
    }
  };



  onMapReady(map: L.map) {
    this.map = map;
    this.searchbox_AutoCompleteCallback(this.previousGeoData);//initialize the address field
    var layer = this.layer;
    var markerOptions = this.markerOptions;

    let searchbox = (document.getElementById('searchbox'));

    let http = this.http;
    var group = this.projectToBeEdit;
    var CallApi = this.callApi;

    this.map.on('click', function (e) {
      console.log(e.latlng);                  // getting the lat lng from mouse
      let updateSearchbox = <HTMLInputElement>searchbox.firstElementChild.firstElementChild.firstElementChild.firstElementChild;
      group.controls["projectLatLng"].patchValue(JSON.stringify(e.latlng));
      CallApi.getLocationByLatLng(http, e.latlng, group).subscribe(
        data => {
          // console.log(data);
          group.controls["projectLocation"].patchValue(data);
          updateSearchbox.value = data;
        });
      map.setView(e.latlng);
      map.eachLayer(function (layer) {
        if ('_level' in layer) return;
        map.removeLayer(layer);
      });
      layer = L.marker(e.latlng, markerOptions);
      map.addLayer(layer);
    });
  }

}



