import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormComponent } from '../../../shared/form/form.component';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../../shared/services";

@Component({
  selector: 'app-add-site-supervisor',
  templateUrl: './add-site-supervisor.component.html',
  styleUrls: ['./add-site-supervisor.component.scss']
})
export class AddSiteSupervisorComponent implements OnInit {

  supervisorForm:any[];
	group:FormGroup;	
	@Output()
	formSubmitSuccess=new EventEmitter<boolean>();


	constructor(private fb:FormBuilder) {
		this.group=this.fb.group({
			name: ['',[Validators.required,Validators.maxLength(50)]],
			email: ['',[Validators.required,Validators.email,Validators.maxLength(50)]],
			password:['',[Validators.required,Validators,Validators.minLength(8)]],
			phone: ['',[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],			
			position: ['Supervisor'],
			projectID: [''],
			dob: ['',[Validators.required,Validators.maxLength(50)]],			
			photo: [''],
			blood: ['',[Validators.required,CustomValidators.validateBloodType,Validators.maxLength(3)]],
			emergencyContactName:['',[Validators.required,Validators.maxLength(50)]],
			emergencyContactPhone: ['',[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],
			height: ['',[Validators.required,Validators.min(0),Validators.max(280)]],
			weight: ['',[Validators.required,Validators.min(10),Validators.max(200)]],
			devid: ['',[Validators.required]]
			
		});

	}

	ngOnInit() {
		this.constructSupervisorForm();
	}

	formSubmit(event){
    console.log("add-supervisor form submitted successfully? " + event);
    this.formSubmitSuccess.emit(event);
	}

	constructSupervisorForm():void{
		this.supervisorForm=[
		{
			form_group:this.group,
			submit_url:ApiCmd.addSupervisor,
			contain_image:true,
			button_name:"Add",
			form_control_names:[
				{
					"input_name":"Name",
					"form_control_name":"name",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExampleName",
					"error_message":"NameError"
				},
				{
					"input_name":"EmailAddress",
					"form_control_name":"email",
					"type":{type:"email",options:[],accept:""},
					"placeholder":"ExampleEmail",
					"error_message":"EmailError"
				},
				{
					"input_name":"Password",
					"form_control_name":"password",
					"type":{type:"password",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"PhoneNumber",
					"form_control_name":"phone",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExamplePhone",
					"error_message":"RequiredError"
				},
				{
					"input_name":"ProjectID",
					"form_control_name":"projectID",
					"special":{type:"select",url:ApiCmd.projectListOngoing,kind:"project",var_loc:1},
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"DOB",
					"form_control_name":"dob",
					"special":{type:"date"},					
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"Photo",
					"form_control_name":"photo",
					"type":{type:"file",options:[],accept:"image/*"},
					"placeholder":"",
					"error_message":""
				},
				{
					"input_name":"Blood",
					"form_control_name":"blood",
					"special":{type:"blood",var_loc:0},
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"emergencyContactName",
					"form_control_name":"emergencyContactName",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"emergencyContactPhone",
					"form_control_name":"emergencyContactPhone",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"Height",
					"form_control_name":"height",
					"type":{type:"number",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"Weight",
					"form_control_name":"weight",
					"type":{type:"number",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
				{
					"input_name":"DeviceID",
					"form_control_name":"devid",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},
			]
			// form_control_names:[
			// 	{
			// 		"input_name":"Name",
			// 		"form_control_name":"name",
			// 		"type":{type:"text",options:[],accept:""},
			// 		"placeholder":"ExampleName",
			// 		"error_message":"NameError"
			// 	},{
			// 		"input_name":"EmailAddress",
			// 		"form_control_name":"email",
			// 		"type":{type:"email",options:[],accept:""},
			// 		"placeholder":"ExampleEmail",
			// 		"error_message":"EmailError"
			// 	},{
			// 		"input_name":"Password",
			// 		"form_control_name":"password",
			// 		"type":{type:"password",options:[],accept:""},
			// 		"placeholder":"",
			// 		"error_message":"PasswordError"
			// 	},{
			// 		"input_name":"CompanyID",
			// 		"form_control_name":"companyID",
			// 		"type":{type:"text",options:[],accept:""},
			// 		"placeholder":"",
			// 		"error_message":"RequiredError"
			// 	},{
			// 		"input_name":"Contact",
			// 		"form_control_name":"phone",
			// 		"type":{type:"text",options:[],accept:""},
			// 		"placeholder":"ExamplePhone",
			// 		"error_message":"ContactError"
			// 	},{
			// 		"input_name":"Photo",
			// 		"form_control_name":"photo",
			// 		"type":{type:"file",options:[],accept:"image/*"},
			// 		"placeholder":"",
			// 		"errorMessage":""
			// 	}
			// ]

		}
	];
	}

}



