import { Component, OnInit, EventEmitter, Output, Input  } from '@angular/core';
import { FormComponent } from '../../../shared/form/form.component';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../../shared/services";

@Component({
  selector: 'app-add-router',
  templateUrl: './add-router.component.html',
  styleUrls: ['./add-router.component.scss']
})
export class AddRouterComponent implements OnInit {
	routerForm:any[];
	group:FormGroup;	
	@Output()
  formSubmitSuccess=new EventEmitter<boolean>();
  @Input()
  LatLngSelected:any;
  
  constructor(private fb:FormBuilder) { 

  }

  ngOnInit() {
    console.log(this.LatLngSelected);
    if(this.LatLngSelected!=null){
      this.group=this.fb.group({
        p_id: ['',[Validators.required,Validators.maxLength(50)]],
        ssid: ['',[Validators.required,Validators.maxLength(50)]],
        mac: ['',[Validators.required,Validators]],
        zone: ['',[Validators.required,Validators.maxLength(50)]],
        lat: [this.LatLngSelected.lat,[Validators.required,Validators.maxLength(50)]],
        lng: [this.LatLngSelected.lng,[Validators.required,Validators.maxLength(50)]],
      });
    }else{
      this.group=this.fb.group({
        p_id: ['',[Validators.required,Validators.maxLength(50)]],
        ssid: ['',[Validators.required,Validators.maxLength(50)]],
        mac: ['',[Validators.required,Validators]],
        zone: ['',[Validators.required,Validators.maxLength(50)]],
        lat: ['',[Validators.required,Validators.maxLength(50)]],
        lng: ['',[Validators.required,Validators.maxLength(50)]],
      });
    }
    this.constructRouterForm();
  }

  formSubmit(event){
    console.log("add-router form submitted successfully? " + event);
    this.formSubmitSuccess.emit(event);
	}

  constructRouterForm():void{
    this.routerForm=[
      {
        form_group:this.group,
        submit_url:ApiCmd.addRouter,
        contain_image:false,
        button_name:"Add",
        form_control_names:[
          {
            "input_name":"ProjectID",
            "form_control_name":"p_id",
            "type":{type:"text",options:[],accept:""},
            "placeholder":"TT001",
            "error_message":"RequiredError"
          },
          {
            "input_name":"SSID",
            "form_control_name":"ssid",
            "type":{type:"text",options:[],accept:""},
            "placeholder":"Office_Floor_1",
            "error_message":"RequiredError"
          },
          {
            "input_name":"MAC",
            "form_control_name":"mac",
            "type":{type:"text",options:[],accept:""},
            "placeholder":"1a:1a:1a:1a:1a:1a",
            "error_message":"RequiredError"
          },
          {
            "input_name":"Zone",
            "form_control_name":"zone",
            "special":{type:"select",url:ApiCmd.getZoneList,kind:"zone",var_loc:0},
            "type":{type:"text",options:[],accept:""},
            "placeholder":"NoZoneSelected",
            "error_message":"RequiredError"
          },
          {
            "input_name":"Latitude",
            "form_control_name":"lat",
            "type":{type:"number",options:[],accept:""},
            "placeholder":"1.0000000",
            "error_message":"RequiredError"
          },
          {
            "input_name":"Longitude",
            "form_control_name":"lng",
            "type":{type:"number",options:[],accept:""},
            "placeholder":"1.0000000",
            "error_message":"RequiredError"
          },
        ]
  
      }
    ];
  }
}



