import { Injectable } from '@angular/core';
// import * as firebase from 'firebase';
import * as firebase from 'firebase/app';
import 'firebase/messaging';

import 'rxjs/add/operator/take';
import { BehaviorSubject } from 'rxjs/BehaviorSubject'
import { CallApiService } from '../call-apis/call-api.service';
import { ApiCmd } from '../call-apis/api-cmd';
import { AuthService } from '../../auth/auth.service';

@Injectable()
export class MessagingService {
  messaging = firebase.messaging()
  currentMessage = new BehaviorSubject(null);

  constructor(private callApi: CallApiService,
              private authService: AuthService) { }

  updateToken(token) {
    let userData = this.authService.getUserData();
    const data = {
      // c_id: userData.company_id,
      id: userData.id,
      token: token
    }
    console.log(data);
    console.log("update token");
    this.callApi.postData(ApiCmd.updatePushToken,JSON.stringify(data))  
    .subscribe((response => {
      console.log("response from token sent to server");
      console.log(response);
    }))
  }
  

  getPermission() {
      this.messaging.requestPermission()
      .then(() => {
        console.log('Notification permission granted.');
        return this.messaging.getToken()
      })
      .then(token => {
        console.log(token)
        this.updateToken(token)
      })
      .catch((err) => {
        console.log('Unable to get permission to notify.', err);
      });
    }

    receiveMessage() {
        this.messaging.onMessage((payload) => {
        console.log("Message received. ", payload);
        this.currentMessage.next(payload)
      });

    }            
}



