import { Component, OnInit,Output,EventEmitter, ViewEncapsulation, Input, ViewChild  } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../shared/services";
import { sha256 } from 'js-sha256';

@Component({
  selector: 'app-form',
	templateUrl: './form.component.html',
	encapsulation:ViewEncapsulation.None,
  styleUrls: ['./form.component.scss']
})
export class FormComponent implements OnInit {
	bloodType:any[] = ["A+","A-","B+","B-","AB+","AB-","O+","o-"];
  @Input() formData:any;
  @Output()
  formSubmitSuccess=new EventEmitter<boolean>();
  formForm:FormGroup;
  data:any;
  gotImage:boolean;
  url:string;
  buttonName:string;
  //to keep the file
  photo:any;
  file:any;
  @ViewChild('fileInput')
	fileInput: any;
	
	itemsArray :any[][]=[];
  constructor(private callApi:CallApiService,
				private reloadService:ApiReloadService) { }
  ngOnInit() {
		this.formForm=this.formData[0].form_group;
		this.data=this.formData[0].form_control_names;
		this.gotImage=this.formData[0].contain_image;
		this.url=this.formData[0].submit_url;
		this.buttonName=this.formData[0].button_name;
		this.findSpecial(this.data);
		console.log(this.formForm);
		console.log(this.data);
  }

  onSubmit(event:any):void{
  	// this.employeeForm.controls["projectID"].patchValue(value.id);
		// event.password=sha256(event.password);
		if(event['dob']!=null){
			event['dob'] = (new Date(event['dob'])).toISOString();
		}
  	console.log(event);
  	console.log(this.gotImage);
  	if(this.gotImage)this.gotImageSubmit(event);
  	else this.noImageSubmit(event);
  }

  gotImageSubmit(event){
	console.log("form to be submit:")
	console.log(event);
  	this.callApi.postWithFile(this.url,event,this.photo)
  	.subscribe((response)=>{
		  let responseBody = response.text();
		  let firstWordOfResponse = responseBody.split(" ")[0];
		  console.log(firstWordOfResponse);
		  if(responseBody==""){
			this.formSubmitSuccess.emit(true);
			this.reloadService.setReload();		  
		  }else if(firstWordOfResponse=="Duplicate"){
			alert("Email Address have been registered, please use another email address");
		  }else{
			alert(responseBody);
		  }

		console.log('response: ');
		console.log(response);
  	});
  }

  noImageSubmit(event){
	console.log("form to be submit:")
	console.log(event);
  	this.callApi.postData(this.url,JSON.stringify(event))
  	.subscribe((response)=>{
		let responseBody = response.text();
		let firstWordOfResponse = responseBody.split(" ")[0];
		if(responseBody==""){
			this.formSubmitSuccess.emit(true);
			this.reloadService.setReload();		  
		  }else if(firstWordOfResponse=="Duplicate"){
			alert("Email Address have been registered, please use another email address");
		  }else{
			alert(responseBody);
		  }		  
		console.log('response: ');
		console.log(response);
			  
  	})
  }

  fileUpload(event){
  	//preview
  	if (event.target.files && event.target.files[0]&&this.isImage(event)) {
  	  var reader = new FileReader();
  	  reader.readAsDataURL(event.target.files[0]);

  	  reader.onload = (event:any) => {
  	    this.file = event.target.result;
  		}
  		this.photo=event.srcElement.files;
  		console.log(this.photo);


  	}else{
  		alert("Please upload image only");
  		this.fileInput.nativeElement.value = "";
  	}

  }

  isImage(event):boolean{
  	let file =event.target.files[0];
  	let fileType = file["type"];
  	fileType=fileType.split("/");
  	if(fileType[0]=="image"){
  		return true;
  	}

  	return false;
	}
	
	findSpecial(data:any[]){
		data.forEach((formElement)=>{
			if(formElement.special&&formElement.special.type=="select"){
				this.getAndAssignToDropdown(formElement.special.url,formElement.special.kind);
				console.log("this element got select");
			}else if(formElement.special&&formElement.special.type=="blood"){
				this.assignBloodTypeToDropdown();
				console.log("this element got blood type selection")
			}
		})
	}

	assignBloodTypeToDropdown(){
		let items:any[]=[];
		this.bloodType.forEach((blood)=>{
			items.push({
				id: blood,
				text: blood
			});
		})
		this.itemsArray.push(items);
	}

	//assign to items variable accroding to ng2-select
	getAndAssignToDropdown(url,kind): void {
		let items:any[] = [];
		// call api to get the list
		this.callApi.getData(url)
		.subscribe(data=>{
			// assign data to variable items
			data.forEach((item) => {
				if(kind=="zone"){
					items.push({
						id: item.zone_id,
						text:item.zone_id
					});
				}else if(kind=="project"){
					items.push({
						id: item.p_id,
						text:item.p_data.p_name
					});
				}
			});
			this.itemsArray.push(items);
		});
	}

	public selected(groupName,value:any):void {
		this.formForm.controls[groupName].patchValue(value.id);
		console.log('Selected value is: ', value);
	}

}



