import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { FormComponent } from '../../../shared/form/form.component';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../../shared/services";

@Component({
  selector: 'app-edit-admin',
  templateUrl: './edit-admin.component.html',
  styleUrls: ['./edit-admin.component.scss']
})
export class EditAdminComponent implements OnInit {
  @Input() adminToBeEdit: any;
  editAdminForm:any[];
  group:FormGroup;
  @Output()
  formSubmitSuccess=new EventEmitter<boolean>();

  constructor(private fb:FormBuilder) { 
  }

  ngOnInit() {
    console.log(this.adminToBeEdit)
    this.group=this.fb.group({
			id:[this.adminToBeEdit.s_id],
			name: [this.adminToBeEdit.s_name,[Validators.required,Validators.maxLength(50)]],
			email: [this.adminToBeEdit.s_email,[Validators.required,Validators.email,Validators.maxLength(50)]],
			password:['',[Validators,Validators.minLength(8)]],
			companyID: [this.adminToBeEdit.company_id,[Validators.required,Validators.maxLength(50)]],
			phone: [this.adminToBeEdit.s_contact,[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],
			photo: ['']
		});
	this.constructAdminForm();
  }

  formSubmit(event){
    console.log("add-admin form submitted successfully? " + event);
    this.formSubmitSuccess.emit(event);
	}

  constructAdminForm():void{
		this.editAdminForm=[
		{
			form_group:this.group,
			submit_url:ApiCmd.updateSuperuser,
			contain_image:true,
			button_name:"Edit",
			form_control_names:[
				{
					"input_name":"Name",
					"form_control_name":"name",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExampleName",
					"error_message":"NameError"
				},{
					"input_name":"EmailAddress",
					"form_control_name":"email",
					"type":{type:"email",options:[],accept:""},
					"placeholder":"ExampleEmail",
					"error_message":"EmailError"
				},{
					"input_name":"Password",
					"form_control_name":"password",
					"type":{type:"password",options:[],accept:""},
					"placeholder":"",
					"error_message":"PasswordError"
				},{
					"input_name":"CompanyID",
					"form_control_name":"companyID",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"",
					"error_message":"RequiredError"
				},{
					"input_name":"Contact",
					"form_control_name":"phone",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExamplePhone",
					"error_message":"ContactError"
				},{
					"input_name":"Photo",
					"form_control_name":"photo",
					"type":{type:"file",options:[],accept:"image/*"},
					"placeholder":"",
					"errorMessage":""
				}
			]

		}
	];
	}

}



